@import url(https://fonts.googleapis.com/css2?family=Baloo+Bhaijaan+2&display=swap);
* {
   box-sizing: border-box;
   padding: 0;
   margin: 0;
}

a {
  text-decoration: none;
}

body {
  margin: 0;
   font-family: 'Baloo Bhaijaan 2', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
   max-width: 1100px;
   margin: 0 auto;
}
.App{padding:20px;text-align:left}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:auto 1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .home-page-section{grid-column:span 4}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:center}.TeamPage .team-name{font-size:4rem}.TeamPage .wins-losses-section{font-size:2rem;padding:30px;justify-self:center;align-self:center;text-align:center}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-link{align-self:top;justify-self:center;font-size:1.5rem}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;margin-bottom:20px;padding:20px;border:1px solid black}.MatchDetailCard .match-date,.MatchDetailCard .match-venue{margin-top:10px}.MatchDetailCard .result-award-other-detail{text-align:left}.MatchDetailCard .result-award-other-detail>p{margin-bottom:20px}

.MatchSmallCard{border:1px solid black;padding:20px;margin-bottom:20px}.MatchSmallCard .match-result{margin-top:10px}

.YearSelector{text-align:center;list-style-type:none}.YearSelector li{margin-bottom:10px;margin-top:10px}

.MatchPage{display:grid;grid-template-columns:1fr 5fr;grid-column-gap:40px}.MatchPage .home-page-section{grid-column:span 4}.MatchPage .year-selector{justify-self:end;padding:20px}.MatchPage .page-heading{margin:20px auto}

.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-row-gap:10px;grid-column-gap:10px}.HomePage .app-name{font-size:3rem;margin:30px auto;text-align:center}

.TeamTile{height:170px;text-align:center;border:1px solid black;padding:20px}

