.TeamPage{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: auto 1fr 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 10px;

    .home-page-section{
        grid-column: span 4;
    }

    .team-name-section{
         grid-column: span 3;
         justify-self: start;
         align-self: center;
    }

    .team-name{
        font-size: 4rem;
    }

    .wins-losses-section{
        font-size: 2rem;
        padding: 30px;
        justify-self: center;
        align-self: center;
        text-align: center;
    }

    .match-detail-section{
        grid-column: span 4;
    }

    .more-link{
        align-self: top;
        justify-self: center;
        font-size: 1.5rem;
    }
}